class UserValidationError extends Error{
    constructor(errors){
        super({
         code:"USER-01",
         statusCode:400,
         message:errors
        })
    }
}
class UserCreationError extends Error{
    constructor(errors){
        super({
         code:"USER-02",
         statusCode:400,
         message:errors
        })
    }
}
class DatabaseConnectionError extends Error{
    constructor(errors){
        super({
         code:"DTB-01",
         statusCode:400,
         message:errors
        })
    }
}
module.exports={
    UserCreationError,
    UserValidationError,
    DatabaseConnectionError
}